<script lang="ts">
    import L, {Map, Popup} from 'leaflet';
    import type {LeafletMouseEvent} from 'leaflet';
    import DmsCoordinates, {Dms} from 'dms-conversion';

    import DmsHelper from '../lib/dms';

    export let node: HTMLElement | undefined = undefined;

    let popupElement: HTMLElement;
    let currentMap: Map;
    let popup: Popup;
    let coords = "";
    let clickedCoords = "";

    interface FormattedLatLng {
        lat: string;
        lng: string;
    }

    export function onControlAdded(map: Map) {
        currentMap = map;
        map.on('mousemove', onMouseMove);
        map.on('contextmenu', onContextMenu);
        popup = L.popup()
            .setContent(popupElement);
    }

    export function onControlRemoved(map: Map) {
        map.off('mousemove', onMouseMove);
        map.off('contextmenu', onContextMenu);
        popup.removeFrom(map);
    }

    function onMouseMove(e: LeafletMouseEvent) {
        const formattedLatLng = formatLatLng(e.latlng.lat, e.latlng.lng);
        const dmsCoords = new DmsCoordinates(e.latlng.lat, e.latlng.lng);
        coords = `${formattedLatLng.lat}, ${formattedLatLng.lng} / ${DmsHelper.toDmsString(e.latlng)}`;
    }

    function onContextMenu(e: LeafletMouseEvent) {
        if (!e.originalEvent.shiftKey && !e.originalEvent.ctrlKey && !e.originalEvent.altKey) {
            clickedCoords = coords;
            if (!currentMap) {
                return;
            }
            popup
                .setLatLng(e.latlng)
                .openOn(currentMap);
        }
    }

    function formatLatLng(lat: number, lng: number): FormattedLatLng {
        return {
            lat: lat.toFixed(6),
            lng: lng.toFixed(6),
        };
    }
</script>

<div bind:this={node} class="coords">
    {#if coords !== ""}
        G {coords}
    {/if}
</div>

<div style="display: none;">
    <div bind:this={popupElement}>
        G {clickedCoords}
    </div>
</div>

<style>
    .coords {
        padding: 4px;
        background-color: rgba(255, 255, 255, 0.7);
    }
</style>