<script lang="ts">
    import L, {LatLng, Map, Popup} from 'leaflet';
    import type {LeafletMouseEvent} from 'leaflet';

    import GpsLocalFrame from '../lib/GpsLocalFrame';

    export let node: HTMLElement | undefined = undefined;
    export let origin: LatLng | null = null;

    let popupElement: HTMLElement;
    let currentMap: Map;
    let popup: Popup;
    let coords = "";
    let clickedCoords = "";
    let gpsLocalFrame: GpsLocalFrame = null;

    $: {
        if (origin) {
            gpsLocalFrame = new GpsLocalFrame(origin.lat, origin.lng);
            coords = "";
            clickedCoords = "";
        } else {
            gpsLocalFrame = null;
            coords = "";
            clickedCoords = "";
        }
    }

    interface FormattedPoint {
        x: string;
        y: string;
    }

    export function onControlAdded(map: Map) {
        currentMap = map;
        map.on('mousemove', onMouseMove);
        map.on('contextmenu', onContextMenu);
        popup = L.popup()
            .setContent(popupElement);
    }

    export function onControlRemoved(map: Map) {
        map.off('mousemove', onMouseMove);
        map.off('contextmenu', onContextMenu);
        popup.removeFrom(map);
    }

    function onMouseMove(e: LeafletMouseEvent) {
        if (!gpsLocalFrame) {
            return;
        }

        const point: number[] = gpsLocalFrame.toLocal(e.latlng.lat, e.latlng.lng)
        const formattedPoint = formatPoint(point[0], point[1]);
        coords = formattedPoint.x + ", " + formattedPoint.y;
    }

    function onContextMenu(e: LeafletMouseEvent) {
        if (!e.originalEvent.shiftKey && !e.originalEvent.ctrlKey && e.originalEvent.altKey) {
            clickedCoords = coords;
            if (!currentMap) {
                return;
            }
            popup
                .setLatLng(e.latlng)
                .openOn(currentMap);
        }
    }

    function formatPoint(x: number, y: number): FormattedPoint {
        return {
            x: x.toFixed(3),
            y: y.toFixed(3),
        };
    }
</script>

<div bind:this={node} class="coords">
    {#if coords !== ""}
        L {coords}
    {/if}
</div>

<div style="display: none;">
    <div bind:this={popupElement}>
        L {clickedCoords}
    </div>
</div>

<style>
    .coords {
        padding: 4px;
        background-color: rgba(255, 255, 255, 0.7);
    }
</style>