<script lang="ts">
    import Router, {push} from 'svelte-spa-router';
    import TopAppBar, {Row, Section, Title} from '@smui/top-app-bar';
    import IconButton from '@smui/icon-button';
    import Drawer, {AppContent, Content, Scrim} from '@smui/drawer';
    import List, {Item, Meta, Separator, Text} from '@smui/list';
    import {FlatToast, ToastContainer} from "svelte-toasts";

    import {Env} from './env';

    import Home from './components/Home.svelte';
    import About from './components/About.svelte';

    const routes = {
        '/': Home,
        '/about': About,
    }

    let drawerOpen = false;

    function goto(url: string) {
        drawerOpen = false;
        push(url);
    }
</script>
<div class="main-wrapper">
    <div>
        <TopAppBar variant="static" dense>
            <Row>
                <Section>
                    <IconButton class="material-icons" on:click={() => drawerOpen = !drawerOpen}>menu</IconButton>
                    <Title>
                        Map Tool
                        {#if !Env.isProduction}
                            <span class="mdc-chip">dev</span>
                        {/if}
                    </Title>
                </Section>
            </Row>
        </TopAppBar>
    </div>
    <div style="flex-grow: 1;">
        <Drawer variant="modal" bind:open={drawerOpen} style="z-index: 2001;">
            <Content>
                <List>
                    <Item on:click={() => goto("#/")}>
                        <Text>Home</Text>
                        <Meta class="material-icons">home</Meta>
                    </Item>
                    <Separator/>
                    <Item on:click={() => goto("#/about")}>
                        <Text>About</Text>
                        <Meta class="material-icons">info</Meta>
                    </Item>
                </List>
            </Content>
        </Drawer>
        <Scrim style="z-index: 2000;"/>
        <AppContent style="height: 100%;">
            <div class="main-container">
                <Router {routes}/>
            </div>
            <ToastContainer placement="bottom-center" theme="dark" showProgress={true} let:data={data}>
                <FlatToast {data}/>
            </ToastContainer>
        </AppContent>
    </div>
</div>
<style>
    .main-wrapper {
        height: 100%;
        display: flex;
        flex-flow: column;
    }

    .main-container {
        height: 100%;
    }
</style>
