<script lang="ts">
    import {createEventDispatcher} from 'svelte';
    import Button, {Label} from '@smui/button';
    import Dialog, {Actions, Content, Title} from '@smui/dialog';
    import LayoutGrid, {Cell} from '@smui/layout-grid';
    import Textfield from '@smui/textfield';
    import HelperText from '@smui/textfield/helper-text';
    import L, {LatLng} from 'leaflet';
    import type UIEvent from '../lib/events';

    const dispatch = createEventDispatcher<{
        set: UIEvent.OriginUpdated,
    }>();

    let latitudeString: string = '';
    let longitudeString: string = '';
    let invalidLatitude: boolean = false;
    let invalidLongitude: boolean = false;
    let open: boolean = false;

    export function show(latLng: LatLng) {
        if (latLng) {
            latitudeString = latLng.lat.toString();
            longitudeString = latLng.lng.toString();
        } else {
            latitudeString = '';
            longitudeString = '';
        }
        open = true;
    }

    function toFloat(s: string): number {
        return Number(s);
    }

    function toLatitude(s: string): number {
        const latitude = toFloat(s);
        if (isNaN(latitude) || (latitude < -90) || (latitude > 90)) {
            return NaN;
        }
        return latitude;
    }

    function toLongitude(s: string): number {
        const longitude = toFloat(s);
        if (isNaN(longitude) || (longitude < -180) || (longitude > 180)) {
            return NaN;
        }
        return longitude;
    }

    function onLatitudeChanged() {
        const latitude = toLatitude(latitudeString);
        invalidLatitude = isNaN(latitude);
    }

    function onLongitudeChanged() {
        const longitude = toLongitude(longitudeString);
        invalidLongitude = isNaN(longitude);
    }

    function onSet() {
        const latitude = toLatitude(latitudeString);
        const longitude = toLongitude(longitudeString);
        if (isNaN(latitude) || isNaN(longitude)) {
            return;
        }
        dispatch('set', {
            latLng: L.latLng(latitude, longitude),
        });
    }
</script>

<Dialog bind:open={open}>
    <Title>Set Origin</Title>
    <Content>
        <LayoutGrid>
            <Cell span={6}>
                <Textfield bind:value={latitudeString} label="Latitude" invalid={invalidLatitude}
                           on:change={() => onLatitudeChanged()}>
                        <HelperText slot="helper">
                            {#if invalidLatitude}
                                Invalid value
                            {/if}
                        </HelperText>
                </Textfield>
            </Cell>
            <Cell span={6}>
                <Textfield bind:value={longitudeString} label="Longitude" invalid={invalidLongitude}
                           on:change={() => onLongitudeChanged()}>
                    <HelperText slot="helper">
                        {#if invalidLongitude}
                            Invalid value
                        {/if}
                    </HelperText>
                </Textfield>
            </Cell>
        </LayoutGrid>
    </Content>
    <Actions>
        <Button on:click={() => onSet()}>
            <Label>Set</Label>
        </Button>
    </Actions>
</Dialog>
