import DmsCoordinates, {Dms} from 'dms-conversion';
import type {LatLng} from "leaflet";

export default class DmsHelper {
    public static toString(dms: Dms): string {
        const dmsArray = dms.getDmsArray();
        return `${dmsArray[0]}${dmsArray[1]}${dmsArray[2].toFixed(1)} ${dmsArray[3]}`;
    }

    public static toDmsString(latLng: LatLng): string {
        const dmsCoords = new DmsCoordinates(latLng.lat, latLng.lng);
        return `${DmsHelper.toString(dmsCoords.latitude)} ${DmsHelper.toString(dmsCoords.longitude)}`;
    }
}
