<script lang="ts">
    import {onDestroy, onMount} from 'svelte';
    import L, {Control, FeatureGroup, LatLng, Map, Marker, Polygon, Polyline} from 'leaflet';
    import Button from '@smui/button';
    import Textfield from '@smui/textfield';
    import type geojson from 'geojson';
    import {toasts} from 'svelte-toasts';
    import type {TrackSymbol} from 'leaflet-tracksymbol';

    import Window from './Window.svelte';
    import MapButton from './MapButton.svelte';
    import SetOriginDialog from './SetOriginDialog.svelte';
    import Coords from './Coords.svelte';
    import LocalCoords from './LocalCoords.svelte';

    import {LeafletHelper} from '../lib/leaflet-helper';
    import MapLayerManager from '../lib/MapLayerManager';
    import type UIEvent from '../lib/events';
    import {geoJsonWritable, originWritable} from '../lib/store';
    import GpsLocalFrame from '../lib/GpsLocalFrame';
    import DmsHelper from '../lib/dms';

    let mapEl: HTMLElement;
    let zoomToFitButton: HTMLElement;
    let setOriginButton: HTMLElement;
    let coordsEl: HTMLElement;
    let coordsComponent: Coords;
    let localCoordsEl: HTMLElement;
    let localCoordsComponent: LocalCoords;
    let editGeoJSONButton: HTMLElement;
    let exportToLocalCoordsButton: HTMLElement;
    let myLocationButton: HTMLElement;
    let map: Map;
    let mapControls: Control[] = [];
    let mapLayerManager: MapLayerManager;
    let editableLayers: FeatureGroup;
    let displayGeoJsonOverlay: boolean = false;
    let displayLocalCoordsOverlay: boolean = false;
    let setOriginDialog: SetOriginDialog;
    let origin: LatLng | null = null;
    let geojsonText: string | null = null;
    let localCoordsText: string | null = null;
    let myLocationTrackSymbol: TrackSymbol | null = null;

    onMount(() => {
        initMap();
        originWritable.subscribe(value => {
            mapLayerManager.setOrigin(value);
            origin = value;
        });
        geoJsonWritable.subscribe(value => {
            const geojsonLayer = L.geoJSON();
            geojsonLayer.addData(<geojson.GeoJsonObject>value);
            editableLayers.clearLayers();
            geojsonLayer.getLayers().forEach(layer => {
                editableLayers.addLayer(layer);
            });
        });
    });

    onDestroy(() => {
        mapControls.forEach(control => map.removeControl(control));
    });

    function initMap() {
        map = L.map(mapEl, {
            center: [1.364917, 103.822872],
            zoom: 11,
        });
        mapLayerManager = new MapLayerManager(map);

        addControl(L.control.scale({
            maxWidth: 200,
        }));
        addControl(L.control.ruler({
            position: 'topleft',
        }));

        const openStreetMapsTileLayer = L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
            minZoom: 0,
            maxZoom: 20,
            maxNativeZoom: 19,
            attribution: " OpenStreetMap contributors",
        });
        const oneMapSgTileLayer = L.tileLayer('https://maps-{s}.onemap.sg/v3/Original/{z}/{x}/{y}.png', {
            minZoom: 11,
            maxZoom: 18,
            bounds: [[1.56073, 104.11475], [1.16, 103.502]],
            attribution: '<img src="https://docs.onemap.sg/maps/images/oneMap64-01.png" style="height:20px;width:20px;"/> New OneMap | Map data &copy; contributors, <a href="http://SLA.gov.sg">Singapore Land Authority</a>',
        });
        const cartoDBVoyagerTileLayer = L.tileLayer('https://{s}.basemaps.cartocdn.com/rastertiles/voyager/{z}/{x}/{y}{r}.png', {
            attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors &copy; <a href="https://carto.com/attributions">CARTO</a>',
            subdomains: 'abcd',
            maxZoom: 19
        });
        const openSeaMapTileLayer = L.tileLayer('https://tiles.openseamap.org/seamark/{z}/{x}/{y}.png', {
            opacity: 0.2,
            attribution: 'Map data: &copy; <a href="http://www.openseamap.org">OpenSeaMap</a> contributors',
        });
        const esriWorldImageryTileLayer = L.tileLayer('https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}', {
            attribution: 'Tiles &copy; Esri &mdash; Source: Esri, i-cubed, USDA, USGS, AEX, GeoEye, Getmapping, Aerogrid, IGN, IGP, UPR-EGP, and the GIS User Community'
        });
        openStreetMapsTileLayer.addTo(map);

        const baseMaps = {
            "OpenStreetMaps": openStreetMapsTileLayer,
            "OneMapSG": oneMapSgTileLayer,
            "CartoDB.Voyager": cartoDBVoyagerTileLayer,
            "Esri.WorldImagery": esriWorldImageryTileLayer,
        };

        const overlayMaps = {
            "OpenSeaMap": openSeaMapTileLayer,
            "Mission": mapLayerManager.missionLayer,
            "Geofence": mapLayerManager.geofenceLayer,
        };

        addControl(L.control.layers(baseMaps, overlayMaps, {}));
        addControl(LeafletHelper.newControl(zoomToFitButton, {
            position: 'topright',
        }));
        addControl(LeafletHelper.newControl(setOriginButton, {
            position: 'topright',
        }));
        addControl(LeafletHelper.newControl(editGeoJSONButton, {
            position: 'topright',
        }));
        addControl(LeafletHelper.newControl(exportToLocalCoordsButton, {
            position: 'topright',
        }));
        addControl(LeafletHelper.newControl(myLocationButton, {
            position: 'topright',
        }));
        addControl(LeafletHelper.newControl(coordsEl, {
            position: 'bottomright',
        }, map => {
            coordsComponent.onControlAdded(map);
        }, map => {
            coordsComponent.onControlRemoved(map);
        }));
        addControl(LeafletHelper.newControl(localCoordsEl, {
            position: 'bottomright',
        }, map => {
            localCoordsComponent.onControlAdded(map);
        }, map => {
            localCoordsComponent.onControlRemoved(map);
        }));

        editableLayers = L.featureGroup();
        editableLayers.addTo(map);

        addControl(new L.Control.Draw({
            draw: {
                circle: false,
                circlemarker: false,
            },
            edit: {
                featureGroup: editableLayers,
                remove: true,
            },
        }));
        map.on(L.Draw.Event.CREATED, e => {
            editableLayers.addLayer(e.layer);
            save(editableLayers);
        });
        map.on(L.Draw.Event.EDITED, _e => {
            save(editableLayers);
        });
        map.on(L.Draw.Event.DELETED, _e => {
            save(editableLayers);
        });
    }

    function save(featureGroup: FeatureGroup) {
        console.log(featureGroup.toGeoJSON());
        geoJsonWritable.set(featureGroup.toGeoJSON());
    }

    function addControl(control: Control) {
        mapControls.push(control);
        control.addTo(map);
    }

    function onZoomToFit() {
        const bounds = mapLayerManager.getBounds();
        if (editableLayers.getBounds().isValid()) {
            bounds.extend(editableLayers.getBounds());
        }
        if (bounds.isValid()) {
            map.fitBounds(bounds);
        }
    }

    function showSetOriginDialog() {
        setOriginDialog.show(origin);
    }

    function onOriginSet(e: CustomEvent<UIEvent.OriginUpdated>) {
        origin = e.detail.latLng;
        mapLayerManager.setOrigin(e.detail.latLng);
        originWritable.set(e.detail.latLng);
    }

    function showGeoJsonDialog() {
        geojsonText = JSON.stringify(editableLayers.toGeoJSON(), null, 2);
        displayGeoJsonOverlay = true;
    }

    function updateGeoJSON() {
        try {
            const o = JSON.parse(geojsonText);
            geoJsonWritable.set(o);
            displayGeoJsonOverlay = false;
        } catch (e) {
            console.log(e);
        }
    }

    function showLocalCoordsDialog() {
        if (!origin) {
            return;
        }
        const gpsLocalFrame = new GpsLocalFrame(origin.lat, origin.lng);
        const converter = new LeafletHelper.Converter(gpsLocalFrame);
        const data = {
            "layers": [],
        };
        editableLayers.getLayers().forEach(layer => {
            if (layer instanceof Polygon) {
                data.layers.push({
                    "type": "Polygon",
                    "items": converter.toLocalArrayList(layer.getLatLngs()),
                });
            } else if (layer instanceof Polyline) {
                data.layers.push({
                    "type": "Polyline",
                    "items": converter.toLocalArrayList(layer.getLatLngs()),
                });
            } else if (layer instanceof Marker) {
                data.layers.push({
                    "type": "Marker",
                    "point": converter.toLocal(layer.getLatLng()),
                });
            }
        })
        localCoordsText = JSON.stringify(data, null, 2);
        displayLocalCoordsOverlay = true;
    }

    function showMyLocation() {
        navigator.geolocation.getCurrentPosition(position => {
            const latLng = L.latLng(position.coords.latitude, position.coords.longitude);
            toasts.add({
                type: 'info',
                description: `${position.coords.latitude} ${position.coords.longitude} / ${DmsHelper.toDmsString(latLng)} / ${position.coords.accuracy.toFixed(1)}m / ${position.coords.heading}`,
                placement: 'bottom-center',
                duration: 0,
            });
            const headingInRadians = (position.coords.heading !== null) ? position.coords.heading * Math.PI / 180.0 : null;
            if (myLocationTrackSymbol) {
                myLocationTrackSymbol.setLatLng(latLng);
                myLocationTrackSymbol.setHeading(headingInRadians);
            } else {
                myLocationTrackSymbol = L.trackSymbol(latLng, {
                    fill: true,
                    fillColor: '#0000ff',
                    fillOpacity: 1.0,
                    stroke: true,
                    color: '#000000',
                    opacity: 1.0,
                    weight: 1.0,
                    heading: headingInRadians,
                });
                myLocationTrackSymbol.addTo(map);
            }
            console.log(position);
        }, error => {
            toasts.add({
                type: 'error',
                description: 'Could not get location: ' + error,
                placement: 'bottom-center',
                duration: 5000,
            });
            console.log(error);
        }, {
            enableHighAccuracy: true,
        });
    }
</script>

<div class="map-container">
    <div bind:this={mapEl} class="map-container"></div>
</div>

<div style="display: none;">
    <MapButton bind:node={zoomToFitButton} icon="crop_free" tooltip="Zoom to fit" on:click={() => onZoomToFit()}/>
    <MapButton bind:node={setOriginButton} icon="adjust" tooltip="Set origin" on:click={() => showSetOriginDialog()}/>
    <MapButton bind:node={editGeoJSONButton} icon="code" tooltip="GeoJSON" on:click={() => showGeoJsonDialog()}/>
    <MapButton bind:node={exportToLocalCoordsButton} icon="output" tooltip="Export to local coords format"
               on:click={() => showLocalCoordsDialog()}/>
    <MapButton bind:node={myLocationButton} icon="my_location" tooltip="My location"
               on:click={() => showMyLocation()}/>

    <Coords bind:this={coordsComponent} bind:node={coordsEl}/>
    <LocalCoords bind:this={localCoordsComponent} bind:node={localCoordsEl} origin={origin}/>
</div>

{#if displayGeoJsonOverlay}
    <div class="overlay">
        <Window title="GeoJSON" on:close={() => displayGeoJsonOverlay = false}>
            <div slot="content" style="height: 100%;">
                <Textfield textarea bind:value={geojsonText} style="width: 100%; height: 100%;">
                </Textfield>
            </div>
            <div slot="footer" style="text-align: right; margin: 8px;">
                <Button variant="raised" on:click={() => updateGeoJSON()}>Update</Button>
            </div>
        </Window>
    </div>
{/if}

{#if displayLocalCoordsOverlay}
    <div class="overlay">
        <Window title="Local coords" on:close={() => displayLocalCoordsOverlay = false}>
            <div slot="content" style="height: 100%;">
                <Textfield textarea bind:value={localCoordsText} style="width: 100%; height: 100%;">
                </Textfield>
            </div>
        </Window>
    </div>
{/if}

<SetOriginDialog bind:this={setOriginDialog} on:set={e => onOriginSet(e)}/>

<style>
    .map-container {
        height: 100%;
    }

    .overlay {
        position: absolute;
        z-index: 2000;
        left: 64px;
        top: 64px;
        right: 64px;
        bottom: 64px;
        background-color: rgba(255, 255, 255, 0.9);
    }
</style>
