import L, {LatLng} from 'leaflet';
import type {Map, ControlOptions} from 'leaflet';
import type GpsLocalFrame from './GpsLocalFrame';

export type Callback = (map: Map) => void;

export namespace LeafletHelper {

    export function newControl(element: HTMLElement, controlOptions?: ControlOptions, addCallback?: Callback, removeCallback?: Callback) {
        const Control = L.Control.extend({
            onAdd: (map: Map) => {
                L.DomEvent.disableClickPropagation(element);
                if (addCallback) {
                    addCallback(map);
                }
                return element;
            },
            onRemove: (map: Map) => {
                if (removeCallback) {
                    removeCallback(map);
                }
            }
        });
        return new Control(controlOptions);
    }

    export class Converter {

        gpsLocalFrame: GpsLocalFrame;

        constructor(gpsLocalFrame: GpsLocalFrame) {
            this.gpsLocalFrame = gpsLocalFrame;
        }

        public toLocal(latLng: LatLng): number[] {
            const localPoint = this.gpsLocalFrame.toLocal(latLng.lat, latLng.lng);
            localPoint[0] = Number(localPoint[0].toFixed(1));
            localPoint[1] = Number(localPoint[1].toFixed(1));
            return localPoint;
        }

        public toLocalArray(latLngs: LatLng[]): number[][] {
            return latLngs.map(latLng => this.toLocal(latLng));
        }

        public toLocalArrayList(latLngs: LatLng[] | LatLng[][] | LatLng[][][]): number[][][] {
            try {
                if (!Array.isArray(latLngs[0])) { // LatLng[]
                    const v = latLngs as LatLng[];
                    return [this.toLocalArray(v)];
                } else if (!Array.isArray(latLngs[0][0])) { // LatLng[][]
                    const v = latLngs as LatLng[][];
                    return v.map(latLngs => this.toLocalArray(latLngs));
                } else if (!Array.isArray(latLngs[0][0][0])) { // LatLng[][][]
                    console.log('cannot handle value', latLngs);
                    return null;
                }
            } catch (e) {
                console.log(e);
                return null;
            }
        }
    }
}
