export class GpsLocalFrame {

    latitude: number;
    longitude: number;

    rLatitude: number;
    latitudeMultiplier: number;
    longitudeMultiplier: number;

    constructor(latitude: number, longitude: number) {
        this.latitude = latitude;
        this.longitude = longitude;

        this.rLatitude = latitude * Math.PI / 180;
        this.latitudeMultiplier = 111132.92
            - 559.82 * Math.cos(2 * this.rLatitude)
            + 1.175 * Math.cos(4 * this.rLatitude)
            - 0.0023 * Math.cos(6 * this.rLatitude);
        this.longitudeMultiplier = 111412.84 * Math.cos(this.rLatitude)
            - 93.5 * Math.cos(3 * this.rLatitude)
            + 0.118 * Math.cos(5 * this.rLatitude);
    }

    toLocal(latitude: number, longitude: number): number[] {
        return [
            (longitude - this.longitude) * this.longitudeMultiplier,
            (latitude - this.latitude) * this.latitudeMultiplier,
        ];
    }

    toGps(x: number, y: number): number[] {
        return [
            (y / this.latitudeMultiplier) + this.latitude,
            (x / this.longitudeMultiplier) + this.longitude,
        ];
    }
}

export default GpsLocalFrame;
