import L, {Map, FeatureGroup, Marker, Polygon, LatLngBounds, LatLng, Polyline} from 'leaflet';
import type {PolylineOptions} from 'leaflet';

export enum PaneId {
    GEOFENCE = 'geofence',
    MISSION = 'mission',
}

const missionPolylineOptions: PolylineOptions = {
    opacity: 0.5,
    dashArray: '4',
    pane: PaneId.MISSION,
};

function toLocation(latLng: LatLng): string | string[] {
    if (!latLng) {
        return '';
    }
    if (latLng.alt) {
        return [latLng.lat.toFixed(6), latLng.lng.toFixed(6), latLng.alt.toFixed(2)];
    } else {
        return [latLng.lat.toFixed(6), latLng.lng.toFixed(6)];
    }
}

export class MapLayerManager {

    map: Map;
    geofenceLayer: FeatureGroup;
    missionLayer: FeatureGroup;
    originMarker: Marker | null = null;
    missionPolyline: Polyline | null = null;
    geofencePolygon: Polygon | null = null;

    constructor(map: Map) {
        this.map = map;

        map.createPane(PaneId.GEOFENCE).style.zIndex = '300';
        map.createPane(PaneId.MISSION).style.zIndex = '301';

        this.geofenceLayer = L.featureGroup([], {
            pane: PaneId.GEOFENCE,
        });
        this.geofenceLayer.addTo(map);

        this.missionLayer = L.featureGroup([], {
            pane: PaneId.MISSION,
        });
        this.missionLayer.addTo(map);
    }

    getBounds(): LatLngBounds {
        const bounds = L.latLngBounds([]);
        const featureGroups: FeatureGroup[] = [
            this.geofenceLayer,
            this.missionLayer,
        ];
        featureGroups
            .filter(featureGroup => this.map.hasLayer(featureGroup) && featureGroup.getBounds().isValid())
            .forEach(featureGroup => bounds.extend(featureGroup.getBounds()));
        return bounds;
    }

    setOrigin(latLng: LatLng) {
        if (!latLng) {
            if (this.originMarker) {
                this.originMarker.remove();
                this.originMarker = null;
            }
            return;
        }
        if (this.originMarker !== null) {
            this.originMarker.setLatLng(latLng);
        } else {
            this.originMarker = L.marker(latLng, {
                title: 'origin',
            });
            this.originMarker.addTo(this.map);
        }
    }

    setGeofence(latLngs: LatLng[]) {
        if (!latLngs) {
            if (this.geofencePolygon) {
                this.geofencePolygon.remove();
                this.geofencePolygon = null;
            }
            return;
        }
        if (this.geofencePolygon !== null) {
            this.geofencePolygon.setLatLngs(latLngs);
        } else {
            this.geofencePolygon = L.polygon(latLngs, {
                interactive: false,
            });
            this.geofencePolygon.bindPopup('geofence');
            this.geofencePolygon.addTo(this.geofenceLayer);
        }
    }

    setMission(latLngs: LatLng[]) {
        if (!latLngs) {
            if (this.missionPolyline) {
                this.missionPolyline.remove();
                this.missionPolyline = null;
            }
            return;
        }
        if (this.missionPolyline !== null) {
            this.missionPolyline.setLatLngs(latLngs);
        } else {
            this.missionPolyline = L.polyline(latLngs, {
                interactive: false,
            });
            this.missionPolyline.bindPopup('mission');
            this.missionPolyline.addTo(this.missionLayer);
        }
    }
}

export default MapLayerManager;
