import {writable} from 'svelte/store';
import L, {LatLng} from "leaflet";
import TypedLocalStore from 'typed-local-store';

interface Origin {
    latitude: number;
    longitude: number;
}

interface StorageSchema {
    origin: Origin;
    geoJson: string;
}

const typedStore = new TypedLocalStore<StorageSchema>();

export const originWritable = writable<LatLng>(null, start => {
    const storedValue = typedStore.getItem('origin', 'safe');
    if (storedValue) {
        start(L.latLng(storedValue.latitude, storedValue.longitude));
    } else {
        start(null);
    }
});

originWritable.subscribe(value => {
    if (value) {
        typedStore.setItem('origin', {
            latitude: value.lat,
            longitude: value.lng,
        });
    } else {
        typedStore.setItem('origin', null);
    }
});

export const geoJsonWritable = writable<Object>(null, start => {
    const storedValue = typedStore.getItem('geoJson', 'safe');
    if (storedValue) {
        start(JSON.parse(storedValue));
    } else {
        start(null);
    }
});

geoJsonWritable.subscribe(value => {
    if (value) {
        typedStore.setItem('geoJson', JSON.stringify(value));
    } else {
        typedStore.setItem('geoJson', null);
    }
});
